árquico (constituido por esquemas). Usualmente, se denomina conocimientos previos.")])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('div',{staticClass:"tarjeta tarjeta--blanca p-5"},[_c('h4',[_vm._v("Conocimiento estratégico")]),_c('p',[_vm._v("Tiene que ver directamente con lo que en este material de formación se denominan estrategias de aprendizaje. Brown de manera acertada lo describe con el nombre de saber cómo conocer.")])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-11"},[_c('div',{staticClass:"tarjeta tarjeta--blanca p-5"},[_c('h4',[_vm._v("Conocimiento metacognitivo")]),_c('p',[_vm._v("Se refiere al conocimiento que poseen los individuos sobre qué y cómo se sabe, así como al conocimiento que poseen las personas sobre los procesos y operaciones cognitivas cuando se aprende, se recuerda o se solucionan problemas.")])])])])])],1)])]),_c('Separador'),_vm._m(10),_c('p',[_vm._v("La cognición, un proceso central en el desarrollo humano, abarca funciones como la atención, percepción, memoria e inteligencia, y se analiza desde diversas disciplinas, incluyendo la psicología, la ingeniería, la lingüística y la educación. Este enfoque interdisciplinar permite entender cómo los individuos conocen, interpretan y toman decisiones en su interacción con el mundo.")]),_vm._m(11),_c('div',{staticClass:"bloque-texto-g bloque-texto-g--inverso color-acento-contenido p-3 p-sm-4 p-md-5 mb-5",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/temas/t1/BG.png')) + ")")})},[_c('img',{attrs:{"src":require("@/assets/curso/temas/t4/img5.png"),"alt":""}})]),_vm._m(12)]),_vm._m(13),_c('Separador'),_vm._m(14),_c('p',[_vm._v("Las estrategias cognitivas en el ámbito pedagógico son técnicas o procedimientos que los estudiantes utilizan para procesar, organizar, comprender y retener la información de manera más 